'use client';

import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import {
  Sheet,
  SheetContent,
  SheetTrigger,
} from '@/components/ui/sheet';
import { Button } from '@/components/ui/button';
import {
  Avatar,
  AvatarFallback,
  AvatarImage,
} from '@/components/ui/avatar';
import { Menu, User, Settings, LogOut } from 'lucide-react';
import AppSidebar from './app-sidebar';
import { usePathname } from 'next/navigation';
import { Switch } from './ui/switch';
import { useLanguage } from '@/contexts/language-context';
import { useUserProfile } from '@/contexts/user-profile-context';
import Link from 'next/link';

const getTitleFromPathname = (pathname: string): string => {
  if (pathname === '/dashboard') return 'Dashboard';
  if (pathname.startsWith('/doc-verification')) return 'Document Verification';
  if (pathname.startsWith('/community')) return 'Community';
  if (pathname.startsWith('/blog')) return 'Blog';
  if (pathname.startsWith('/about')) return 'About Us';
  if (pathname.startsWith('/contacts')) return 'Contact';
  if (pathname.startsWith('/settings')) return 'Settings';
  if (pathname.startsWith('/profile')) return 'My Profile';
  return 'Mannova';
};

interface AppHeaderProps {
    language: 'en' | 'ml';
    setLanguage: (lang: 'en' | 'ml') => void;
}

export default function AppHeader({ language, setLanguage }: AppHeaderProps) {
  const pathname = usePathname();
  const { t } = useLanguage();
  const { profile } = useUserProfile();
  const title = getTitleFromPathname(pathname);

  const handleLogout = () => {
    // Perform logout logic here, then redirect
    window.location.href = '/login';
  };

  return (
    <header className="flex h-16 items-center gap-4 border-b bg-card px-4 md:px-6 sticky top-0 z-30">
      <div className="md:hidden">
        <Sheet>
          <SheetTrigger asChild>
            <Button variant="outline" size="icon">
              <Menu className="h-5 w-5" />
              <span className="sr-only">Toggle navigation menu</span>
            </Button>
          </SheetTrigger>
          <SheetContent side="left" className="p-0 flex gap-0">
            <AppSidebar />
          </SheetContent>
        </Sheet>
      </div>

      <h1 className="text-lg font-semibold md:text-xl">{title}</h1>

      <div className="flex w-full items-center gap-4 md:ml-auto md:gap-2 lg:gap-4 justify-end">
        <div className="flex items-center justify-center space-x-2">
            <span className="text-sm">English</span>
            <Switch
              checked={language === 'ml'}
              onCheckedChange={(checked) => setLanguage(checked ? 'ml' : 'en')}
            />
            <span className="text-sm">മലയാളം</span>
        </div>
        <DropdownMenu>
          <DropdownMenuTrigger asChild>
            <Button variant="secondary" size="icon" className="rounded-full">
              <Avatar>
                <AvatarImage src={profile.avatar} />
                <AvatarFallback>{profile.name.charAt(0)}</AvatarFallback>
              </Avatar>
              <span className="sr-only">Toggle user menu</span>
            </Button>
          </DropdownMenuTrigger>
          <DropdownMenuContent align="end">
            <DropdownMenuLabel>{profile.name}</DropdownMenuLabel>
            <DropdownMenuSeparator />
            <Link href="/profile" passHref legacyBehavior>
                <DropdownMenuItem>
                    <User className="mr-2 h-4 w-4" />
                    <span>{t('headerProfile', 'common')}</span>
                </DropdownMenuItem>
            </Link>
             <Link href="/settings" passHref legacyBehavior>
                <DropdownMenuItem>
                    <Settings className="mr-2 h-4 w-4" />
                    <span>{t('headerSettings', 'common')}</span>
                </DropdownMenuItem>
            </Link>
            <DropdownMenuSeparator />
            <DropdownMenuItem onClick={handleLogout}>
                <LogOut className="mr-2 h-4 w-4" />{t('headerLogout', 'common')}
            </DropdownMenuItem>
          </DropdownMenuContent>
        </DropdownMenu>
      </div>
    </header>
  );
}
