'use client';

import Link from 'next/link';
import {
  Home,
  FileCheck2,
  Users,
  BookOpen,
  Info,
  Mail,
  Leaf,
  Settings,
} from 'lucide-react';
import { usePathname } from 'next/navigation';
import { cn } from '@/lib/utils';
import { Button } from './ui/button';
import { Card, CardContent, CardHeader, CardTitle } from './ui/card';
import { useLanguage } from '@/contexts/language-context';
import { generateTipsAction } from '@/app/actions';
import { useEffect, useState } from 'react';
import { Skeleton } from './ui/skeleton';

const navItems = [
  { href: '/dashboard', icon: Home, label: 'navDashboard' },
  { href: '/doc-verification', icon: FileCheck2, label: 'navDocVerification' },
  { href: '/community', icon: Users, label: 'navCommunity' },
  { href: '/blog', icon: BookOpen, label: 'navBlog' },
  { href: '/about', icon: Info, label: 'navAbout' },
  { href: '/contacts', icon: Mail, label: 'navContact' },
  { href: '/settings', icon: Settings, label: 'navSettings' },
];

export default function AppSidebar() {
  const pathname = usePathname();
  const { t, language } = useLanguage();
  const [tip, setTip] = useState('');
  const [loadingTip, setLoadingTip] = useState(true);

  useEffect(() => {
    async function fetchTip() {
        setLoadingTip(true);
        try {
            const response = await generateTipsAction({
                cropType: "Paddy", // Example data
                landType: "Loamy",
                currentTasks: "Pre-planting",
            });
            setTip(response.tip);
        } catch (error) {
            console.error("Failed to generate tip:", error);
            setTip(t('sidebarTipError', 'common'));
        } finally {
            setLoadingTip(false);
        }
    }
    fetchTip();
  }, [t]);


  return (
    <div className="flex h-full max-h-screen flex-col gap-2 bg-card text-card-foreground">
      <div className="flex h-16 items-center border-b px-4 lg:px-6">
        <Link href="/dashboard" className="flex items-center gap-2 font-semibold">
          <Leaf className="h-6 w-6 text-primary" />
          <span className="">Mannova</span>
        </Link>
      </div>
      <div className="flex-1">
        <nav className="grid items-start px-2 text-sm font-medium lg:px-4">
          {navItems.map((item) => (
            <Link
              key={item.label}
              href={item.href}
              className={cn(
                'flex items-center gap-3 rounded-lg px-3 py-2 text-muted-foreground transition-all hover:text-primary',
                {
                  'bg-muted text-primary': pathname === item.href,
                }
              )}
            >
              <item.icon className="h-4 w-4" />
              {t(item.label, 'common')}
            </Link>
          ))}
        </nav>
      </div>
      <div className="mt-auto p-4">
        <Card>
          <CardHeader className="p-2 pt-0 md:p-4">
            <CardTitle>{t('sidebarTipTitle', 'common')}</CardTitle>
          </CardHeader>
          <CardContent className="p-2 pt-0 md:p-4 md:pt-0">
             {loadingTip ? (
                <div className="space-y-2">
                    <Skeleton className="h-4 w-full" />
                    <Skeleton className="h-4 w-3/4" />
                </div>
            ) : (
                <p className="text-xs text-muted-foreground mb-2">
                 {tip}
                </p>
            )}
            <Button size="sm" className="w-full mt-2">
              {t('sidebarLearnMore', 'common')}
            </Button>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}
