'use client';

import { createContext, useContext, useState, ReactNode } from 'react';

// Mock user profile data
const mockProfile = {
  name: 'R. Kumar',
  farmerId: 'MN-FARM-12345',
  avatar: 'https://images.unsplash.com/photo-1615751072497-5f5169225a1f?q=80&w=1974&auto=format&fit=crop',
  joinDate: 'January 15, 2024',
  gamification: {
    totalPoints: 12530,
    leaderboardRank: 7,
    level: 5,
    levelProgress: 60, // 60%
    pointsToNextLevel: 800,
    badges: [
      { name: 'Eco-Warrior', description: 'Completed 10 soil health tasks', icon: 'Leaf' },
      { name: 'Water-Saver', description: 'Completed 5 water management tasks', icon: 'Droplets' },
    ],
  },
};

type UserProfile = typeof mockProfile;

interface UserProfileContextType {
  profile: UserProfile;
  updateProfile: (newProfile: Partial<UserProfile>) => void;
}

const UserProfileContext = createContext<UserProfileContextType | undefined>(undefined);

export function UserProfileProvider({ children }: { children: ReactNode }) {
  const [profile, setProfile] = useState<UserProfile>(mockProfile);

  const updateProfile = (newProfileData: Partial<UserProfile>) => {
    setProfile(prevProfile => ({ ...prevProfile, ...newProfileData }));
  };

  return (
    <UserProfileContext.Provider value={{ profile, updateProfile }}>
      {children}
    </UserProfileContext.Provider>
  );
}

export function useUserProfile() {
  const context = useContext(UserProfileContext);
  if (context === undefined) {
    throw new Error('useUserProfile must be used within a UserProfileProvider');
  }
  return context;
}
